@font-face {
  font-family: "Inter";
  src: url(./Inter/Inter-Regular.woff) format("woff"),
        url(./Inter/Inter-Regular.woff2) format("woff2");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 500;
  font-display: swap;
  src: url(./Inter/Inter-Medium.woff2) format("woff2"),
      url(./Inter/Inter-Medium.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 900;
  font-display: swap;
  src: url(./Inter/Inter-Black.woff2) format("woff2"),
      url(./Inter/Inter-Black.woff) format("woff");
}